body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.instruments {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    background-color: #444c57;
    padding-bottom: 1em;
}
.instruments div {
    margin-top: 1em;
    margin-bottom: 1em;
    transition: .5s;
    color: white;
}

.instruments div:hover {
    background-color: lightcoral;
    border-radius: 30px;
}

.instruments img {
    height: 200px;
    width: 200px;
    margin-left: 2em;
    margin-right: 2em;
    margin-top: 2em;
    border-radius: 8px;
}

.instruments button {
    margin-bottom: 1em;
    border-radius: 8px;
    display: inline-block;
    border-width: 0px;
    padding: 1em;
    cursor: pointer;
    transition: .3s;
}

.instruments button:hover {
    background-color: #444c57;
    color: white;
}

.criteria {
    background-color: white;
    border-radius: 8px;
    width:-webkit-max-content;
    width:max-content;
    justify-content: center;
    margin-left: 35%;
    padding: 4px;
}

body {
    background-color: #444c57;
}

h1 {
    color: white;
}
h2{
    color: #444c57;
}
.cart {
    color: white;
}

#filtering button {
    margin-bottom: 1em;
    border-radius: 8px;
    display: inline-block;
    border-width: 0px;
    padding: 1em;
    cursor: pointer;
    transition: .3s;
    color: white;
    background-color: #444c57;
}

.miscName {
    margin-top: -3px;
}

.cartName {
    color: white;
}
